
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import Panel from 'vux/src/components/panel/index.vue'

import navFix from "../fix_nav"
const list = () => [{key:"1",value:'精选'}, {key:"2",value:'美食'}, {key:"3",value:'电影'}, {key:"4",value:'酒店'}, {key:"5",value:'外卖'}]

export default {
components: {
Tab,
TabItem,
Sticky,
Divider,
Swiper,
SwiperItem,
SwipeoutButton,
Swipeout,
navFix,
Panel,
SwipeoutItem
},
data () {
return {
disabled: false,
index01: 0,
list2: list(),
demo2: '美食',
list3: [],
demo3: '收到的消息',
list4: [],
demo4: '即将上映',
demoDisabled: 'A',
index: 0,
type: '1',
}
},
watch: {
index: function () {
this.getGoodsByType(this.list2[this.index].key)
}
}
,
mounted: function () {
this.getTypes()
this.getGoodsByType(this.list2[this.index].key)
},
methods: {
getTypes(){
this.$http.get('http://localhost:8000/getGoodsTypes').then(response => {
this.list2=this.querySetToArray(response.data)




}, response => {
console.log("error");
});
},
querySetToArray(obj){
var newObj=[]
for( var i=0;i<obj.length;i++){
newObj.push(
{
key:obj[i].pk,
value:obj[i].fields.name
})
}
return newObj
},
getGoodsByType (index) {







this.$http.get('http://localhost:8000/getGoodsList?type_id='+index).then(response => {
this.list3=response.data;



}, response => {
console.log("error");
});
}
}
}



